import React, {Component} from 'react';

export default class Input extends Component {
    
    state = {
        value: '',
        error: '',
        errorMessage: this.props.errorMessage || "Bad input"
    }
    
    onChangeInput=({ target : {value}}) => {
        this.setState({
            value,
            error: ''
        })
    }
    onBlurInput=({ target : {value}}) => {
        let pattern = new RegExp('^\\+380(50|67|97|66|63|99|95|98)[0-9]{7}$');
        let rezult = pattern.test(value);
        console.log(rezult);
        this.setState({
            error: !rezult
        })
    }
    
    render(){
        const {type} = this.props;

        const {value, error, errorMessage} = this.state;

        return (
            <div>
                <input 
                
                type={type}
                className={error ? "error" : ''}
                value={value}
                onChange={this.onChangeInput}
                onBlur={this.onBlurInput}

                />
                {error ? <p>{errorMessage}</p> : null}
            </div>
            
        )
    }
}
