import React, {Component, Fragment} from 'react';

class ListItem extends Component{

    state={
        posts:[]
    }

    async componentDidMount() {
        let rezult = await fetch('https://jsonplaceholder.typicode.com/posts')
        rezult = await rezult.json();
        const data = rezult.filter((el) => {
            return el.id > 10 && el.id < 15
        })

        this.setState({
            posts: data
        })
    }
    returnLi = (data) => {
        const{ body, id, title, userId } = data;
        return (
            <li className='content-list' key={id}>
                <h2>{title}</h2>
                <p>{body}</p>
            </li>
        )
    
    }

    render(){
      const {posts} = this.state;
 
      return (
        <div>
          <ul>
              {
                  posts.map(post => {
                      return this.returnLi(post)
                  })
              }
          </ul>
        </div>
      )
    }
  }
  
  export default ListItem;

